        // url
    	getQueryString: function(name) {
    		var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    		var r = window.location.search.substr(1).match(reg);
    		if (r != null) return (r[2]); return null;
    	},
    	getRequest: function () {
		    var url = location.search; //url"?"
		    var theRequest = new Object();
		    if (url.indexOf("?") != -1) {
		        var str = url.substr(1);
		        strs = str.split("&");
		        for(var i = 0; i < strs.length; i ++) {
		            theRequest[strs[i].split("=")[0]]=(strs[i].split("=")[1]);
		        }
		    }
		    return theRequest;
		},
        // url
        setRequest: function (url, params) {
            if (typeof(params) != 'object') return url
            var paramsStrLIst = [];
            for (var key in params) {
                var pattern= new RegExp(key + '=([^&]*)&?');
                url = url.replace(pattern, '')
                paramsStrLIst.push(key + '=' + params[key])
            }
            if(url.match('[\?]')){
                return (url+'&' + paramsStrLIst.join('&')).replace('?&', '?');
            }else{
                return url+'?' + paramsStrLIst.join('&');
            }
        },
        throttle: function (func, wait, options) {
              var context, args, result;
              var timeout = null;
              // 
              var previous = 0;
              if (!options) options = {};
              // 
              var later = function() {
                // 0
                previous = options.leading === false ? 0 : (new Date).valueOf();
                timeout = null;
                result = func.apply(context, args);
                if (!timeout) context = args = null;
              };
              return function() {
                var now = (new Date).valueOf();
                // 
                if (!previous && options.leading === false) previous = now;
                // 
                var remaining = wait - (now - previous);
                context = this;
                args = arguments;
                // remaining0
                // remainingwait
                if (remaining <= 0 || remaining > wait) {
                  clearTimeout(timeout);
                  timeout = null;
                  previous = now;
                  result = func.apply(context, args);
                  if (!timeout) context = args = null;
                //
                } else if (!timeout && options.trailing !== false) {
                  timeout = setTimeout(later, remaining);
                }
                return result;
              };
        },
        // rem
        correctrem: function () {
            var rem_full_width = $("<div id='rem-test' style='width:7.5rem'></div>").appendTo($('body')).width();
            var device_width = document.documentElement.clientWidth;
            if (rem_full_width != device_width) {
                document.documentElement.style.fontSize = (device_width / 7.5) * (device_width / rem_full_width) + 'px';
            }
            $('#rem-test').remove();
        },
        // toast
        toast: function (msg, time,cb) {
            var $toastEl = $('<p class="jky-toast">' + msg + '</p>').appendTo($('body'));
            setTimeout(function () {
                $toastEl.remove()
                cb && cb()
            }, (time || 3000))
            return $toastEl
        },
